.mainBox {
  display: flex;
  justify-content: center;
  background-color: #283239;
  margin: 5%;
  box-shadow: 1px 1px 10px 2px rgba(152, 0, 255, 0.3);
  padding: 20px;
}

.landing-div {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  text-align: center;
}

.feature-container-div {
  margin: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.feature-div {
  padding: 30px;
  margin: 20px;
  min-width: 250px;
  border: 0.5px solid #9800ff;
}

.prof-div {
  text-align: center;
}

.prof-features-container {
  margin: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.prof-feature-div {
  padding: 30px;
  margin: 20px;
  min-width: 250px;
  border: 0.5px solid #9800ff;
  border-radius: 6px;
  box-shadow: 1px 1px 10px 2px rgba(152, 0, 255, 0.3);
}

.prof-feature-div h4 {
  margin-bottom: 20px;
}

.indiv-game-div {
  width: 80%;
  justify-content: flex-start;
  text-align: left;
}

.prof-game-img {
  height: 60px;
  width: 60px;
  border-radius: 10px;
  margin: 5px 10px 5px 0;
}

.prof-squad-div {
  border: 0.1px solid #d81284;
  margin: 10px 0;
  padding: 10px 10px 0;
  box-shadow: 1px 1px 3px 1.5px rgba(216, 18, 132, 0.5);
}

.squads-main {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.squads-main h2 {
  color: #d81284;
}

.squads-utilities {
  min-width: 250px;
  max-width: 400px;
}

.squad-div-container {
  margin: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.indiv-squad-main {
  padding: 30px;
  margin: 20px;
  width: 300px;
  border: 0.5px solid #d81284;
  border-radius: 6px;
  box-shadow: 1px 1px 10px 2px rgba(216, 18, 132, 0.5);
}

.squad-details-div {
  margin: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.squad-details-div h3 {
  color: #d81284;
}

.squad-details {
  padding: 30px;
  margin: 20px;
  min-width: 250px;
  border: 0.5px solid #d81284;
  border-radius: 6px;
  box-shadow: 1px 1px 10px 2px rgba(216, 18, 132, 0.5);
}

.squad-details-div Button {
  margin: 10px;
}

.forums-main {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.forums-main h2 {
  color: #84d812;
}

.indiv-forum-main {
  padding: 30px;
  margin: 20px;
  width: 300px;
  border: 0.5px solid #84d812;
  border-radius: 6px;
  box-shadow: 1px 1px 5px 2px rgba(132, 216, 18, 0.5);
}

.settings-div {
  text-align: center;
  width: 400px;
}

.settings-div h3 {
  margin-bottom: 20px;
}

.account-settings-main {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.acc-containers-macro {
  margin: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.account-containers {
  margin: 20px;
  width: 300px;
}

.chatlog-div {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.chatlog-div > div {
  min-width: 300px;
}

.chat-detail-div {
  padding: 20px 20px 0;
  margin: 20px;
  width: 300px;
  border: 0.5px solid #d81284;
  border-radius: 6px;
  box-shadow: 1px 1px 10px 2px rgba(216, 18, 132, 0.5);
}

.chatContainer {
  width: 300px;
  max-height: 500px;
  border: 5px solid #0091ff;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
}

.chatContainer .messages {
  flex: 80%;
  width: 100%;
  padding-left: 10px;
}

.scroll-container {
  max-height: 500px;
  display: flex;
  flex-direction: column-reverse;
  overflow: scroll;
  padding-bottom: 10px;
}

.chatContainer .messageInputs {
  flex: 20%;
  width: 100%;
  display: flex;
  flex-direction: row;
}

.chatContainer .messageInputs input {
  flex: 80%;
  height: calc(100% -5px);
  border: none;
  border-top: 5px solid #0091ff;
  padding-left: 20px;
  padding-bottom: 0;
  font-size: 20px;
  height: 40px;

}

.chatContainer .messageInputs button {
  flex: 20%;
  height: 40px;
  width: 60px;
  background-color: #0091ff;
  border: none;
  color: white;
  font-size: 18px;
  border-top: 6px solid #0091ff;
}

.messageContainer {
  display: flex;
  /* width: calc(100% - 30px); */
}

.messageContainer h1 {
  color: white;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 300;
  font-size: 17px;
}

#You {
  justify-content: flex-end;
  padding-right: 0;
}

#Other {
  justify-content: flex-start;
}

#Other .messageIndividual {
  background-color: #5ff064;
  color: black;
}
.messageIndividual {
  width: 200px;
  /* height: 60px; */
  border-radius: 10px;
  display: grid;
  padding: 5px;
  /* place-items: center; */
  background-color: #0091ff;
  opacity: 0.9;
  color: white;
  font-family: Arial, Helvetica, sans-serif;
  margin-right: 10px;
  margin-top: 20px;
}

.postContentDiv{
  padding: 30px;
  margin: 20px;
  width: 300px;
  border: 0.5px solid #84d812;
  border-radius: 6px;
  box-shadow: 1px 1px 5px 2px rgba(132, 216, 18, 0.5);
}